.loader {
  letter-spacing: 2px;
  font-size: 30px;
  position: relative;
}

.loader:before {
  content: "";
  color: #fff;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ff0;
  border-radius: 50%;
  margin: auto;
  animation: motion 3s ease-in-out infinite;
  position: absolute;
  top: -70px;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes motion {
  0%, 50%, 100% {
    transform: translateX(0)scale(1);
  }

  25% {
    transform: translateX(-100px)scale(.3);
  }

  75% {
    transform: translateX(100px)scale(.3);
  }
}

.breed-select {
  border: 4px solid #9acd32;
  border-radius: 10px;
  margin-left: 250px;
  padding: 10px;
}

img {
  width: 100%;
  height: auto;
  margin-top: 30px;
  display: block;
}

.container {
  max-width: 660px;
  padding: 15px;
}

