.loader {
  position: relative;
  font-size: 30px;
  letter-spacing: 2px;
}
.loader:before {
  content: '';
  color: #fff;
}
.loader:after {
  content: '';
  width: 20px;
  height: 20px;
  background-color: yellow;
  border-radius: 50%;
  position: absolute;
  inset: 0;
  margin: auto;
  top: -70px;
  animation: motion 3s ease-in-out infinite;
}

@keyframes motion {
  0%,
  50%,
  100% {
    transform: translateX(0) scale(1);
  }
  25% {
    transform: translateX(-100px) scale(0.3);
  }
  75% {
    transform: translateX(100px) scale(0.3);
  }
}

.breed-select {
  padding: 10px 10px;
  border-radius: 10px;
